.home {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100vh;
}

.titleCont h1,
.titleCont p {
  color: whitesmoke;
}

.titleCont h1 {
  font-size: 2rem;
}

.titleCont p {
  font-size: 1rem;
}

.titleCont a:link,
.titleCont a:visited {
  text-decoration: none;
  color: whitesmoke;
  font-size: 1rem;
  border: 1px solid whitesmoke;
  padding: 0.3rem 0.5rem;
  border-radius: 0.5rem;
  transition: 1s;
  box-shadow: black 3px 2px 3px 0.5px;
}

.titleCont a:hover {
  color: rgb(234, 157, 34);
  background-color: whitesmoke;
}

.titleCont a:active {
  color: whitesmoke;
  background-color: rgb(234, 157, 34);
}

.imgCont {
  height: 280px;
  margin: 1rem;
}

.imgCont img {
  height: 100%;
  max-width: 100%;
  border-radius: 2rem;
}

@media only screen and (max-width: 780px) {
  .home {
    flex-wrap: wrap;
    align-content: center;
  }
  .titleCont {
    margin-bottom: 1rem;
  }
}

@media only screen and (max-width: 475px) {
  .titleCont {
    width: 90%;
    text-align: center;
  }

  .titleCont h1 {
    font-size: 1.7rem;
  }

  .titleCont p {
    font-size: 0.8rem;
  }

  .imgCont img {
    height: 200px;
    margin: 0.5rem;
  }
}
